.product-container {
    display: flex;
    justify-content: space-evenly;
    margin-top: 2%;
    margin-bottom: 7%;
}
.product {
    width: 15em;
    height: 21em;
}
.product-img {
    width: 100%;
    height: 100%;
    border-radius: 10em;
    box-shadow: 0px 0px 3px rgb(0, 0, 0, 0.73);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
}
.product-hover-background {
    width: 100%;
    height: 100%;
    border-radius: 10em;
    cursor: pointer;
}
.product-hover-background:hover {
    background-color: rgba(0, 0, 0, 0.5);
    animation: product-hover-state  2s linear infinite;
}
.product-name {
    text-align: center;
    margin: 4px;
    letter-spacing: 4px;
    font-family: cursive;
}
.product-price {
    text-align: center;
    margin: 4px;
    font-weight: 700;
}
.product-hover-background svg {
    font-size: 2em;
    color: #fff;
    border: 1px solid #fff;
    padding: 5px;
    border-radius: 50%;
    display: none;
    position: relative;
    top: 80%;
    left: 40%;
    transition: all 2s linear;
}

@keyframes product-hover-state {
    0% {
        box-shadow: 1px -1px 10px -2px red;
    }
    20% {
        box-shadow: 1px 0px 10px -2px red;

    }
    40% {
        box-shadow: 0px 1px 10px -2px red;
    }
    60% {
        box-shadow: 0px 1px 10px -2px blue;
    }
    80% {
        box-shadow: -1px 0px 10px -2px blue;

    }
    100% {
        box-shadow: -1px -1px 10px -2px blue;
    }
    
}
@media (max-width: 767px) {
    .product {
        margin-bottom: 50px;
    }
    .product-hover-background svg  {
        top: 70%;
        left: 20%;
    }
}
@media (min-width: 768px) and (max-width: 1199px) {
 .product-container {
    gap: 20px;
 }
}